import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import { useState } from 'react';

function CreateNote(props) {
    const [entry,showEntry]=useState(false);
    const [note,setNote]=useState({
        title:"",
        content:""
    })
    const handleChange = (event) =>{
        const {name,value} = event.target;
        setNote((prevData)=>{
            return {
                ...prevData,
                [name]:value
            }
        })
    }
    const handleSubmit = ()=>{
        props.addNote(note);
        setNote({
        title:"",
        content:""
        })
    }
    const show = ()=>{
        showEntry(true);
    }
    const hide = ()=>{
        showEntry(false);
    }

  return (
  <>
    <div className='main_note' >
        <form>

            {entry?        
            <input 
            onChange={handleChange} 
            value={note.title} 
            name='title'
            type='text' 
            placeholder='Title' 
            autoComplete='off' />
            :null}
            <textarea 
            rows='' 
            column='' 
            onChange={handleChange} 
            onClick={show}
            onDoubleClick={hide}
            value={note.content} 
            name='content'
            placeholder="Write a Note" > 
            </textarea>
            {entry?
            <Button onClick={handleSubmit} >
                <AddIcon className="plus_sign" />
            </Button>
            :null}
        </form>
    </div>
  </>
  );
}

export default CreateNote; 
